//CSS dla podstrony Osiągnięcia - nowa wersja menu, dla API jednowarstwowego
.contentContainer{
  background-color: $body-bg;
  margin: 25px;
}

.container{
  .categoriesContent{
    width: 100%;
    margin: 0px 25px 25px;
    background-color: rgb(231, 231, 231);
    padding: 10px;
    border-radius: 4px;
    }
  
  .menuContainer{
    position: relative;
    z-index: 10;
    
    .menu{
      display:flex;
      font-size: 1.5rem;
      .menuMainCard{
        .mainCategory{
          cursor: pointer;
          padding: 15px;
        }
        .focused{
          background-color: rgb(233, 233, 233);
        }
      }
      .childrens{
        position: absolute;
        left: 100%;
        top: 0;
        width: max-content;
        .child{
          padding: 15px;
          display:flex;
          position: relative;
          width: 100%;
          cursor: pointer;
        }
        .child:hover{
          background-color: rgb(233, 233, 233);
        }
      }
    }
  }
}

@media screen and (max-width:900px){
.container{
  .menuContainer{
    .menu{
      font-size: 1rem;
    }

  }
}
}

@media screen and (min-width:950px){
  .contentContainer{
    display: flex;
    justify-content: center;
    .container{
      width: 70%;
    }
    
  }
}
